*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Rubik", sans-serif;
}

.header {
  background-image: url("./images/pattern-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 17.5em;
  text-align: center;
  color: #fff;
  position: relative;
}

.header h1 {
  padding: 2.062rem 0 1.9375rem 0;
  font-weight: 500;
  font-size: 2rem;
}

.search {
  display: flex;
  margin: 0 auto;
  max-width: 555px;
}

.search input {
  width: 497px;
  height: 58px;
  border-radius: 15px 0 0 15px;
  border: none;
  outline: none;
  padding-left: 24px;
}

.search-arrow {
  background: hsl(0, 0%, 17%);
  width: 58px;
  height: 58px;
  border-radius: 0 15px 15px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.content {
  max-width: 1110px;
  margin: 0 auto;
}

.search-content {
  position: relative;
  z-index: 2;
  background: #fff;
  display: flex;
  justify-content: space-between;
  max-width: 1046px;
  padding: 32px 37px;
  margin: -64px auto;
  border-radius: 15px;
}

.search-content-location,
.search-content-timezone,
.search-content-isp {
  border-left: 1px solid hsl(0, 0%, 59%);
  padding-left: 50px;
}

.search-content-address {
  border: 0;
}

.search-content-isp {
  padding-right: 32px;
}

.search-content h2 {
  font-size: 12px;
  color: hsl(0, 0%, 59%);
  font-weight: 700;

  text-transform: uppercase;
  letter-spacing: 1.75px;
}

.search-content p {
  font-size: 26px;
  font-weight: 500;
  text-transform: uppercase;
  padding-top: 13px;
  letter-spacing: -0.23px;
}

#mapid {
  height: 80vh;
  z-index: 1;
}
